<template>
  <div>
    <el-row>
      <el-col :span="12">
        <el-button type="warning" @click="addCom">()</el-button>
      </el-col>
    </el-row>
    <el-table :data="tableData" border style="width:80%">
      <el-table-column prop="id" label="ID" width="100"></el-table-column>
      <el-table-column prop="com_name" label="" width="150"></el-table-column>
      <el-table-column prop="login_name" label="" width="150"></el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-tag v-if="scope.power_level === 1"></el-tag>
          <el-tag v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="success" size="mini" @click="getComdetail(scope.row)"></el-button>
          <el-button type="success" size="mini" @click="changeP(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="width:80%">
      <el-button-group style="float:right;margin-top:20px">
        <el-button type="primary" icon="el-icon-arrow-left" @click="getPrepage"></el-button>
        <el-button type="primary" @click="getNexpage">
          
          <i class="el-icon-arrow-right el-icon--right"></i>
        </el-button>
      </el-button-group>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="comdetVis" width="40%" @close="comdeClose">
      <!--  -->
      <el-card>
        <el-row>
          <el-col :span="6">
            <el-tag type="primary" effect="dark"></el-tag>
          </el-col>
          <el-col :span="2">:</el-col>
          <el-col :span="16">
            <el-tag :model="this.comdet.n">{{this.comdet.com_name}}</el-tag>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-tag type="primary" effect="dark"></el-tag>
          </el-col>
          <el-col :span="2">:</el-col>
          <el-col :span="16">
            <el-tag :model="this.comdet.n">{{this.comdet.s ===0? "":""}}</el-tag>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-tag type="primary" effect="dark"></el-tag>
          </el-col>
          <el-col :span="2">:</el-col>
          <el-col :span="16">
            <el-tag :model="this.comdet.login_name">{{this.comdet.login_name}}</el-tag>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-tag type="primary" effect="dark"></el-tag>
          </el-col>
          <el-col :span="2">:</el-col>
          <el-col :span="16">
            <el-tag :model="this.comdet.reg_datetime">{{this.comdet.reg_datetime}}</el-tag>
            <!-- <input type="text" disabled v-model="this.comdet.n" /> -->
          </el-col>
        </el-row>
      </el-card>
      <span slot="footer" class="dialog-footer">
        <el-button @click="comdeClose"></el-button>
        <!--  -->
        <!-- <el-button type="primary" @click="addEnty"> </el-button> -->
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogvisiable" width="40%" @close="addialogClose">
      <!--  -->
      <el-form ref="addcomFormRef" :model="cominfo" :rules="addFormRules" label-width="100px">
        <el-form-item label="" prop="n">
          <el-input v-model="cominfo.n"></el-input>
        </el-form-item>
        <el-form-item label="" prop="s">
          <el-select v-model="cominfo.s" placeholder="">
            <el-option v-for="item in options" :key="item.s" :label="item.label" :value="item.s"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="u">
          <el-input v-model="cominfo.u"></el-input>
        </el-form-item>
        <el-form-item label="" prop="p">
          <el-input v-model="cominfo.p"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addialogClose"> </el-button>
        <el-button type="primary" @click="addCompany"> </el-button>
      </span>
    </el-dialog>
      <!--  -->
    <el-dialog title="" :visible.sync="editInfoVis" width="40%" @close="editClose">
      <!--  -->
      <el-form ref="editcomFormRef" :model="editInfo" :rules="editFormRules" label-width="100px">
        <el-form-item label="" prop="n">
          <el-input v-model="editInfo.n" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="u">
          <el-input v-model="editInfo.u" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="p">
          <el-input v-model="editInfo.p"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editClose"> </el-button>
        <el-button type="primary" @click="editCompany"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      editInfoVis: false,
      // 
      editInfo: {
        n: '',
        s: '',
        u: '',
        p: '',
        id: ''
      },
      // 
      num: 1,
      // 
      prePage: false,
      // 
      nextPage: false,
      tableData: [],
      // 
      comdetVis: false,
      // 
      comdet: {},
      // 
      addDialogvisiable: false,
      // 
      cominfo: {},
      // 
      addFormRules: {
        n: [{ required: true, message: '', trigger: 'blur' }],
        s: [{ required: true, message: '', trigger: 'change' }],
        u: [{ required: true, message: '', trigger: 'blur' }],
        p: [{ required: true, message: '', trigger: 'blur' }]
      },
      options: [
        {
          s: 0,
          label: ''
        },
        {
          s: 1,
          label: ''
        }
      ]
    }
  },
  created () {
    // 
    this.getComlist()
  },
  methods: {
    // 
    async getComlist () {
      const res = await this.$http.post(
        `/microsign/api/adm/comlist`,
        {
          num: this.num,
          size: 10
        }
      )
      this.prePage = res.data.hasPreviousPage
      this.nextPage = res.data.hasNextPage
      this.tableData = res.data.list
    },
    // 
    getPrepage () {
      if (this.prePage) {
        this.num = this.num - 1
        this.getComlist()
      } else {
        this.$message.warning('')
      }
    },
    // 
    getNexpage () {
      if (this.nextPage) {
        this.num = this.num + 1
        this.getComlist()
      } else {
        this.$message.warning('')
      }
    },
    // 
    async getComdetail (row) {
      this.comdet = row
      this.comdetVis = true
    },
    // 
    comdeClose () {
      this.comdetVis = false
      this.comdet = {}
    },
    // 
    addCom () {
      this.addDialogvisiable = true
    },
    addCompany () {
      this.$refs.addcomFormRef.validate(async valid => {
        if (!valid) return
        // this.cominfo = JSON.stringify(this.cominfo)
        const res = await this.$http.post(
          '/microsign/api/adm/com',
          this.cominfo
        )
        if (res.status !== 200) return this.$message.error('')
        // 
        this.addDialogvisiable = !this.addDialogvisiable
        this.getComlist()
      })
    },
    addialogClose () {
      this.$refs.addcomFormRef.resetFields()
      this.addDialogvisiable = false
    },
    // 
    changeP (row) {
      this.editInfo.n = row.com_name
      this.editInfo.id = row.id
      this.editInfo.u = row.login_name
      this.editInfoVis = !this.editInfoVis
    },
    // 
    async editCompany () {
      // const res = this.$http.post(`/microsign/api/adm/com`, {
      //   p: this.editInfo.p,
      //   id: this.editInfo.id
      // })
      // if (res.r !== 0) return this.$message.warning('')
      this.$message.success('')
      this.editInfoVis = !this.editInfoVis
    }
  }
}
</script>

<style scoped>
.el-row {
  height: 30px;
  margin-bottom: 10px;
}
.el-table {
  margin-top: 60px;
}
</style>
