<template>
  <el-container>
    <el-table :data="userList" border style="width: 100%">
      <el-table-column type="index" label="#"></el-table-column>
      <el-table-column prop="name" label="" width="100"></el-table-column>
      <el-table-column prop="buid" label="" width="100"></el-table-column>
      <el-table-column prop="unit" label="" width="100"></el-table-column>
      <el-table-column prop="roomid" label="" width="100"></el-table-column>
      <el-table-column prop="stat" label="" width="100">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.stat === '-1'" type="success"></el-tag>
          <el-tag v-else-if="scope.row.stat === '0'" type="warning"></el-tag>
          <el-tag v-else-if="scope.row.stat === '1'" type="success"></el-tag>
          <el-tag v-else type="warning"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="cpn" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="getOwn(scope.row.uid)"
          ></el-button>
          <el-button
            type="warning"
            icon="el-icon-delete"
            size="mini"
            @click="delPeo(scope.row.uid)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="userDetailVis" width="80%" @close="userDetailClose">
      <!--  -->
      <el-table :data="userDetail" border style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="dt" label="" width="100"></el-table-column>
        <el-table-column prop="stat" label="" width="100">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.stat === '0'" type="warning"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="eid" label="id" width="100"></el-table-column>
        <el-table-column prop="stat" label="" width="100">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.stat === '0'" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.stat === '1'" type="warning">/</el-tag>
            <el-tag v-if="scope.row.stat === '2'" type="warning"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="cpn" label=""></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userDetailClose"></el-button>
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      // 
      userList: [],
      // id
      comid: '',
      // 
      pagenum: '',
      // 
      ulistData: {},
      // 
      userDetail: [],
      // 
      userDetailVis: false
    }
  },
  created () {
    this.comid = window.sessionStorage.getItem('groupid')
    // 
    // 
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      this.ulistData = { groupid: Number(this.comid) }
      // this.ulistData = JSON.stringify(this.ulistData)
      const res = await this.$http.post(
        '/microsign/api/com/userlist',
        this.ulistData
      )
      this.userList = res.data
    },
    // 
    async getOwn (id) {
      const param = { groupid: Number(this.comid), uid: id }
      const res = await this.$http.post('/microsign/api/com/trace', param)
      this.userDetail = res.data
      // 
      this.userDetailVis = true
    },
    // 
    userDetailClose () {
      this.userDetailVis = false
    },
    // 
    delPeo (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          const params = {
            groupid: Number(this.comid),
            uid: id
          }
          const res = this.$http.post(`/microsign/api/com/userdel`, params)
          if (res.status !== 200) {
            return this.$message.warning('')
          }
          this.$message({
            type: 'success',
            message: '!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>

