<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <!--  -->
      <el-form
        ref="loginFormRef"
        status-icon
        :model="loginForm"
        :rules="loginFormRules"
        class="login_from"
        label-width="0"
      >
        <el-form-item prop="u">
          <el-input v-model="loginForm.u" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="p">
          <el-input type="password" v-model="loginForm.p" prefix-icon="iconfont icon-showpassword"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="danger" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!--  -->
    <div class="svip_login">
      <el-button size="mini" type="success" @click="gotoVip"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      loginForm: {
        u: '',
        p: ''
      },
      // 
      entry_list: {},
      // 
      loginFormRules: {
        // 
        u: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: ['blur', 'change']
          }
        ],
        // 
        p: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      // 
      // window.sessionStorage.setItem('comid', 1)
      // this.$router.push('/home')
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        // const p = this.$md5(this.loginForm.p)
        // this.loginForm.p = p
        const params = {
          u: this.loginForm.u,
          p: this.$md5(this.loginForm.p)
        }
        const res = await this.$http.post(
          '/microsign/api/adm/login',
          params
        )
        // 
        if (res.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1.token,sessionStorage
        window.sessionStorage.setItem('token', res.data.token)
        window.sessionStorage.setItem('groupid', res.data.groupid)
        //   1.1 API,
        //   1.2 token ,tokensessionStorage
        // 2.,/home
        // 3.sessionStorage
        this.$router.push('/home-svip')
      })
    },
    gotoVip () {
      this.$router.push('/login')
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-image: url(../assets/imgs/bg.jpg);
  background: url(../assets/imgs/bg.jpg) 0 center no-repeat;
  background-size: cover;
  height: 100%;
  position: relative;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_from {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
.svip_login {
  position: absolute;
  bottom: 0;
  right: 0;
}
</style>
