<template>
  <div>
    <el-row>
      <el-col :span="6"></el-col>
      <el-col :span="12">
        <el-button
          type="primary"
          icon="el-icon-edit"
          size="mini"
          @click="addDialogvisiable = true"
        ></el-button>
      </el-col>
    </el-row>
    <div class="info">
      <el-table :data="DoorData" border style="width: 60%">
        <el-table-column prop="name" label="" width="100"></el-table-column>
        <el-table-column prop label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="changeEntyVis(scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delEnty(scope.row.id)"
            ></el-button>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="getPicout(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="getPicin(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogvisiable" width="50%" @close="addialogClose">
      <!--  -->
      <el-form ref="addFormRef" :model="entrytip" :rules="addFormRules" label-width="70px">
        <el-form-item label="" prop="n">
          <el-input v-model="entrytip.n"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addialogClose"> </el-button>
        <el-button type="primary" @click="addEnty"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="changeDialogvisiable"
      width="50%"
      @close="changedialogClose"
    >
      <!--  -->
      <el-form ref="changeFormRef" :model="entyinfo" :rules="changeFormRules" label-width="70px">
        <el-form-item label="" prop="name">
          <el-input v-model="entyinfo.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeDialogvisiable = false"> </el-button>
        <el-button type="primary" @click="changeEnty"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="imgDia"
      title=""
      :visible.sync="imgVisable"
      width="300px"
      @close="this.imgVisable = !this.imgVisable"
    >
      <div>
        <img :src="imgUrl" class="ewm" alt />
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      comid: '',
      DoorData: [],
      // 
      addDialogvisiable: false,
      changeDialogvisiable: false,
      // 
      entrytip: {
        n: ''
      },
      // 
      addData: {},
      //
      addFormRules: {
        n: [{ required: true, message: '', trigger: 'blur' }]
      },
      changeFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      n: '',
      // id
      id: '',
      // 
      entyinfo: {
        id: '',
        name: ''
      },
      // 
      changeData: {},
      // 
      imgUrl: '../assets/logo.png',
      // 
      imgVisable: false
    }
  },
  created () {
    // 
    this.getEntylist()
    this.getcomid()
  },
  methods: {
    // id
    getcomid () {
      this.comid = window.sessionStorage.getItem('groupid')
    },
    // 
    getEntylist () {
      // sessionStorage,
      this.DoorData = JSON.parse(window.sessionStorage.getItem('entry_list'))
    },
    // 
    addialogClose () {
      this.addDialogvisiable = false
      this.$refs.addFormRef.resetFields()
    },
    addEnty () {
      //  
      this.addData = {
        n: this.entrytip.n,
        groupid: Number(this.comid)
      }
      // this.addData = JSON.stringify(addDate)
      this.$refs.addFormRef.validate(async valid => {
        const res = await this.$http.post(
          '/microsign/api/com/entry',
          this.addData
        )
        if (res.status !== 200) return this.$message.warning('')
        // 
        this.DoorData.push({
          id: res.data.id,
          name: this.entrytip.n
        })
        this.addDialogvisiable = false
      })
    },
    changeEnty () {
      const changeDate = {
        id: this.entyinfo.id,
        groupid: Number(this.comid),
        n: this.entyinfo.name
      }
      this.changeData = JSON.stringify(changeDate)
      this.$refs.changeFormRef.validate(async valid => {
        const res = await this.$http.post(
          '/microsign/api/com/entry',
          this.changeData
        )
        if (res.status !== 200) {
          this.changeDialogvisiable = false
          this.$message.warning('')
          return false
        }
        this.changeDialogvisiable = false
        this.DoorData.map(item => {
          if (item.id === this.entyinfo.id) {
            item.name = this.entyinfo.name
          }
        })
        window.sessionStorage.setItem('entry_list', JSON.stringify(this.DoorData))
        this.id = res.data.id
        this.$message.success('')
      })
    },
    changeEntyVis (info) {
      this.entyinfo = info
      // this.id = info.id
      this.changeDialogvisiable = true
    },
    async delEnty (id) {
      const resConfirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (resConfirm === 'confirm') {
        // ,
        let delInfo = {
          id: id,
          groupid: Number(this.comid)
        }
        // delInfo = JSON.stringify(delInfo)
        const res = await this.$http.post(
          '/microsign/api/com/entry/del',
          delInfo
        )
        if (res.status !== 200) return this.$message.error('')
        const DoorData1 = []
        this.DoorData.map(item => {
          if (item.id !== id) {
            DoorData1.push(item)
          }
        })
        this.DoorData = DoorData1
        this.$message.success('')
      } else {
        this.$message.info('')
      }
    },
    // 
    async getPicout (id) {
      this.imgVisable = !this.imgVisable
      let outurl = `http://localhost:8081/microsign/api/com/qrcode/${this.comid}/${id}/0`
      this.imgUrl = outurl
    },
    async getPicin (id) {
      this.imgVisable = !this.imgVisable
      let inurl = `http://localhost:8081/microsign/api/com/qrcode/${Number(
        this.comid
      )}/${id}/1`
      this.imgUrl = inurl
    }
  }
}
</script>

<style lang='less' scoped>
.el-row {
  width: 100%;
  height: 30px;
}
.imgDia {
  img {
    padding-left: 20px;
    width: 200px;
    height: 200px;
  }
  .el-dialog__body {
    height: 190px;
  }
}
.el-dialog__body {
  height: 300px;
}
.info {
  position: relative;
  img {
    position: absolute;
    right: 0px;
    top: 0px;
  }
}
</style>
