<template>
  <el-container class="pinfo">
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <el-form ref="form" :model="date" label-width="130px">
          <el-form-item label="">
            <el-col :span="11">
              <el-date-picker type="date" placeholder="" v-model="date.ts" format="yyyy-MM-dd"
      value-format="yyyyMMdd" style="width: 100%;"></el-date-picker>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
        <!--  -->
        <el-card>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="sign_in" label="" width="180"></el-table-column>
            <el-table-column prop="sign_out" label="" width="180"></el-table-column>
          </el-table>
        </el-card>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-button @click="excel" type="success">excel</el-button>
        <el-table :data="inoutData" border style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="uid" label="Id" width="100"></el-table-column>
          <el-table-column prop="name" label="" width="180"></el-table-column>
          <el-table-column prop="dt" label=""></el-table-column>
          <el-table-column prop="v" label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.v === '-1'" type="warning"></el-tag>
              <el-tag v-else-if="scope.row.v === '0'" type="warning"></el-tag>
              <el-tag v-else-if="scope.row.v === '1'" type="warning"></el-tag>
              <el-tag v-else-if="scope.row.v === '2'" type="warning"></el-tag>
              <el-tag v-else type="success"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="tt" label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.tt === '0'"></el-tag>
              <el-tag v-else-if="scope.row.tt === '1'">/</el-tag>
              <el-tag v-else-if="scope.row.tt === '2'"></el-tag>
              <el-tag v-else type="success"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="cpn" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <!--  -->
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="getUdetail(scope.row.uid)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>

    <!--  -->
    <el-dialog title="" :visible.sync="userDetailVis" width="70%" @close="userDetailClose">
      <!--  -->
      <el-table :data="userDetail" border style="width: 100%">
        <el-table-column prop="name" label="" width="80"></el-table-column>
        <el-table-column prop="buid" label="" width="50"></el-table-column>
        <el-table-column prop="unit" label="" width="60"></el-table-column>
        <el-table-column prop="roomid" label="" width="60"></el-table-column>
        <el-table-column prop="stat" label="" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.stat === '-1'" type="success"></el-tag>
            <el-tag v-else-if="scope.row.stat === '0'" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.stat === '1'" type="success"></el-tag>
            <el-tag v-else type="warning"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="cpn" label=""></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userDetailClose"></el-button>
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
// import echarts from 'echarts'
export default {
  data () {
    return {
      comid: '',
      date: {
        ts: ''
      },
      tableData: [],
      inoutData: [],
      data1: [],
      // 
      userDetail: [],
      // 
      userDetailVis: false
    }
  },
  created () {
    this.getcomid()
    this.getPeo()
    this.getDetails()
  },
  methods: {
    onSubmit () {
      // ,20200202,moment,date
      // 
      this.getPeo()
    },
    getcomid () {
      this.comid = window.sessionStorage.getItem('groupid')
    },
    // 
    async getPeo () {
      const para = {
        id: Number(this.comid),
        ts: this.date.ts
      }
      const res = await this.$http.post(`/microsign/api/com/stat`, para)
      // jsonpush.
      this.data1.push(res.data)
      // 
      this.tableData = this.data1
      // ,
      this.data1 = []
    },
    // 
    async getDetails () {
      const res = await this.$http.get(`/microsign/api/com/details/${Number(this.comid)}`)
      // 
      this.inoutData = res.data
    },
    // excel
    async excel () {
      const exParam = {
        groupid: Number(this.comid)
      }
      const res = await this.$http.post(`/microsign/api/com/output`, exParam, { responseType: 'blob' })
      let blob = res.data
      const reader = new FileReader()
      reader.readAsDataURL(blob) // base64ahref
      reader.onload = e => {
        // a
        let a = document.createElement('a')
        a.style.display = 'none'
        a.download = '.xlsx'
        a.href = e.target.result
        // const body = document.body
        document.body.appendChild(a) // firefoxclick
        a.click()
        document.body.removeChild(a)
      }
    },
    // 
    async getUdetail (id) {
      const ude = {
        uid: id,
        groupid: Number(this.comid)
      }
      const res = await this.$http.post('/microsign/api/com/user', ude)
      this.userDetail.push(res.data)
      this.userDetailVis = true
    },
    // 
    userDetailClose () {
      this.userDetailVis = false
      this.userDetail = []
    }

  }
}
</script>

<style lang='less' scoped>
.pinfo {
  height: 100%;
  width: 100%;
  position: relative;
  .el-tabs {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .el-card {
    width: 37%;
    margin-left: 200px;
  }
}
</style>
